// @flow
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Row, Col, Card, Button } from 'react-bootstrap';
import classNames from 'classnames';

// components
import PageTitle from '../../../components/PageTitle';
import Table from '../../../components/Table';

// dummy data
import { invoices } from './data';

/* invoice column render */
const InvoiceColumn = ({ row }) => {
    return (
        <>
            <Link to="#" className="text-body fw-bold">
                #{row.original.invoice_id}
            </Link>
        </>
    );
};

/* invoice project id column */
const InvoiceProjectColumn = ({ row }) => {
    return (
        <>
            <Link to="#" className="text-body fw-bold">
                #{row.original.project_id}
            </Link>
        </>
    );
};

/* invoice date column render */
const InvoiceDateColumn = ({ row }) => {
    return (
        <>
            {row.original.invoice_date} <small className="text-muted">{row.original.invoice_time}</small>
        </>
    );
};

/* invoice column render */
const StatusColumn = ({ row }) => {
    return (
        <>
            <h5>
                <span
                    className={classNames('badge', {
                        'badge-success-lighten': row.original.invoice_status === 'Paid',
                        'badge-info-lighten': row.original.invoice_status === 'New',
                        'badge-warning-lighten': row.original.invoice_status === 'Approved',
                    })}>
                    {row.original.invoice_status === 'Paid' && <i className="mdi mdi-coin me-1"></i>}
                    {row.original.invoice_status === 'New' && <i className="mdi mdi-cash me-1"></i>}
                    {row.original.invoice_status === 'Approved' && (<i className="mdi mdi-timer-sand me-1"></i>
                    )}
                    {row.original.invoice_status}
                </span>
            </h5>
        </>
    );
};

/* action column render */
const ActionColumn = ({ row }) => {
    return (
        <>
            <Link to="#" className="action-icon">
                {' '}
                <i className="mdi mdi-eye"></i>
            </Link>
            <Link to="#" className="action-icon">
                {' '}
                <i className="mdi mdi-square-edit-outline"></i>
            </Link>
            <Link to="#" className="action-icon">
                {' '}
                <i className="mdi mdi-delete"></i>
            </Link>
        </>
    );
};

// get all columns
const columns = [
    {
        Header: 'Invoice ID',
        accessor: 'invoice_id',
        sort: true,
        Cell: InvoiceColumn,
    },
    {
        Header: 'Date',
        accessor: 'invoice_date',
        sort: false,
        Cell: InvoiceDateColumn,
    },
    {
        Header: 'Total',
        accessor: 'total',
        sort: false,
    },
    {
        Header: 'Client Name',
        accessor: 'client_name',
        sort: false,
    },
    {
        Header: 'Project ID',
        accessor: 'project_id',
        sort: false,
        Cell: InvoiceProjectColumn,
    },
    {
        Header: 'Status',
        accessor: 'invoice_status',
        sort: false,
        Cell: StatusColumn,
    },
    {
        Header: 'Action',
        accessor: 'action',
        sort: false,
        classes: 'table-action',
        Cell: ActionColumn,
    },
];

// get pagelist to display
const sizePerPageList = [
    {
        text: '10',
        value: 10,
    },
    {
        text: '20',
        value: 20,
    },
    {
        text: '50',
        value: 50,
    },
];

// main component
const Invoices = (): React$Element<React$FragmentType> => {
    const [invoiceList, setInvoiceList] = useState(invoices);

    // change invoice status group
    const changeInvoiceStatusGroup = (InvoiceStatusGroup) => {
        let updatedData = invoices;
        //  filter
        updatedData =
            InvoiceStatusGroup === 'All'
                ? invoices
                : [...invoices].filter((o) => o.invoice_status?.includes(InvoiceStatusGroup));
        setInvoiceList(updatedData);
    };

    return (
        <>
            <PageTitle
                title={'Invoices'}
            />

            <Row>
                <Col xs={12}>
                    <Card>
                        <Card.Body>
                            <Row className="mb-2">
                                <Col xl={8}>
                                    <form className="row gy-2 gx-2 align-items-center justify-content-xl-start justify-content-between">
                                        <div className="col-auto">
                                            <div className="d-flex align-items-center w-auto">
                                                <label htmlFor="status-select" className="me-2">
                                                    Status
                                                </label>
                                                <select
                                                    className="form-select"
                                                    id="status-select"
                                                    onChange={(e) => changeInvoiceStatusGroup(e.target.value)}>
                                                    <option value="All">All</option>
                                                    <option value="New">New</option>
                                                    <option value="Approved">Approved</option>
                                                    <option value="Paid">Paid</option>
                                                </select>
                                            </div>
                                        </div>
                                    </form>
                                </Col>

                                <Col xl={4}>
                                    <div className="text-lg-end mt-xl-0 mt-2">
                                        <Button variant="danger" className="mb-2 me-2">
                                            <i className="mdi mdi-basket me-1"></i> Create
                                        </Button>
                                        <Button variant="light" className="mb-2">
                                            Export
                                        </Button>
                                    </div>
                                </Col>
                            </Row>

                            <Table
                                columns={columns}
                                data={invoiceList}
                                pageSize={10}
                                sizePerPageList={sizePerPageList}
                                isSortable={true}
                                pagination={true}
                                isSelectable={true}
                                isSearchable={true}
                                theadClass="table-light"
                                searchBoxClass="mb-2"
                            />
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </>
    );
};

export default Invoices;